import React, {useState, useEffect} from 'react'
import useObserver from './useObserver'
import Story from "./Story";
import VisualStory from './VisualStory';
import Push from './Push';
import push1 from './medias/push1.jpg'
import push2 from './medias/push2.jpg'
import push3 from './medias/push3.jpg'
import arrowLeft from './Icons/arrowLeft.svg'

function FirstPart({onClick, display}) {
    return (
        <div className="firstpart" style={{
            display: display
        }}>
            <p className="banner-title">SS21 collection</p>
            <span>&nbsp; - &nbsp;</span>
            <a href="#">Learn more</a>
            <div onClick={onClick} className="chevron"></div>
        </div>
    )
}

function SecondPart({onClick, display}) {

    const [contentRef,
        visible] = useObserver({root: null, rootMargin: "0px", treshold: 0.8})

    return (
        <div
            onClick={onClick}
            style={{
            display: display
        }}
            className="inner-banner">
                <div className="col-23">
                    <img src={arrowLeft} />
                    <p className="text-s light">Back to homepage</p>
                </div>
            <div ref={contentRef} className={`col-11 col-m-18 col-s-23 justify-center mgbt-1 mgbt-s-2 align-start flex-col hidden ${visible && "reveal"}`}>
                
                <h1
                    className="text-xxl text-custom light mgbt-text-10">SS21 Collection</h1>
                <p className="text-s medium-light">But I must explain to you
                    how all this mistaken idea of denouncing pleasure and praising pain was born and
                    I will give you a complete account of the system, and expound the actual
                    teachings of the great explorer</p>
            </div>
            <Push img1={push1} img2={push2} img3={push3}/>
            <Push img1={push3} img2={push1} img3={push2}/>
            <Push img1={push1} img2={push2} img3={push3}/>
        </div>
    )
}

export default function Banner() {

    const [height,
        setHeight] = useState("56px")

    const [displayFirst,
        setDisplayFirst] = useState("flex")

    const [displaySecond,
        setDisplaySecond] = useState("none")

    const [overflow,
        setOverflow] = useState("auto")

    document.body.style.overflow = overflow

    function open() {
        if (height === "56px") {
            setHeight("100vh");
            setDisplayFirst("none");
            setOverflow("hidden")
            setTimeout(() => {
                setDisplaySecond("flex")
            }, 500);

        } else {
            setHeight("56px");
            setDisplaySecond("none");
            setOverflow("auto")
            setTimeout(() => {
                setDisplayFirst("flex")
            }, 500);
        }
    }
    return (
        <div className="banner z10" style={{
            height: `${height}`
        }}>
            {height === "56px"
                ? <FirstPart display={displayFirst} onClick={open}/>
                : <SecondPart display={displaySecond} onClick={open}/>}
        </div>
    )
}